import React, { useEffect } from 'react'
import styled from 'styled-components'
import Bg from '../images/headerbg.jpg'
import ReactPlayer from 'react-player'
import Aos from 'aos'
import "aos/dist/aos.css"

const Header = () => {
    useEffect(() => {
        Aos.init({
            duration: 3000
        })
    }, [])
    return (
        <Section>
            <div className="header">
                <div className="overlay"></div>
                <div className="container">
                    <div className="row d-flex justify-content-center">
                        <div data-aos="fade-up" className="col-lg-6 col-md-6 col-sm-12">
                            <h1><span>Adana kebabga</span> hush kelibsiz</h1>
                            <p>Ajoyib taomlar yetkazib beramiz</p>
                            <button className="btn btn-outline-black rounded-pill">bizning menuy</button>
                        </div>
                            
                            <div class="col-lg-6 col-md-6 col-sm-12 d-flex align-items-center justify-content-center aos-init aos-animate" data-aos="zoom-in">
                                <a id="video" href="https://youtu.be/L1mAtyBI77I" class="venobox play-btn vbox-item" dataVbtype="video" dataAutoplay="true"></a>
                            </div>
                    </div>
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
    background: url(${Bg}) no-repeat center center/cover;
    height: 100vh;
    position: relative;
    z-index: 0;
    .row{
        height: 70vh;
        justify-content: center;
        align-items: center;
        .play-btn{
            border: 5px solid #fac564;
            border-radius: 50%;
            width: 8em;
            opacity: 1;
            transition: 3s ease;
            height: 8em;
            &:hover{
                background: #fac564;
            }
        }
    
        h1{
            font-size: 45px;
            font-weight: 700;
            /* font-family: 'Opan-sans'; */
            text-transform: uppercase;
            color: white;
            span{
                color: #fac564;
            }
            @media (max-width: 500px){
            font-size: 30px;
           }
        }
        p{
            font-size: 25px;
            color: white;
            @media (max-width: 500px){
            font-size: 20px;
           }
        }
        .btn{
            border: 1px solid #fac564;
            padding: 8px 25px;
            color: white;
            &:hover{
                background: #fac564;
                color: black;
            }
        }
        .play-btn::after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-40%) translateY(-50%);
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-left: 15px solid #fff;
    z-index: 100;
    transition: all 400ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
    }
    @media (max-width: 500px){
        text-align: center;
        h1{
            font-size: 30px;
        }
    }
`
export default Header