import React from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'

const Footer = () => {
    return (
        <Section>
            <div className="container">
                <div className="row justify-content-center align-items-center">
                    <div className="col-lg-6 col-md-6 col-sm-12">
                        <div className="adres">
                            <p>TEL: +99871 224 04 44|<Link> +99899 004 40 44</Link> </p>
                            
                            <p>Юнусабадский район улица Мойкурган 7 квартал рядом с домом 47 Ташкент UZ, 100190, Узбекистан</p>
                        </div>
                    </div>
                    <div className="col-lg-6 col-md-6 col-sm-12">
                        <div className="adres1 mb-2">
                            <a href="#"><i class="fab fa-telegram-plane fa-2x"></i></a>
                            <a href="#"><i class="fab fa-twitter fa-2x ms-5"></i></a>
                            <a href="#"><i class="fab fa-instagram fa-2x ms-5"></i></a>
                        </div>
                    </div>
                </div>
                <p className="text-center mt-4 text-white">© 2021 by Murat Kusherbaev. Proudly created with It-Academy</p>
            </div>
        </Section>
    )
}
const Section = styled.div`
padding: 5em 0;
background: #000;   
@media (max-width: 500px){
    padding: 2em 0;
}
    .row{
        border-bottom: 1px solid white;
        padding: 3em 0;
        h3{
            text-align: center;
            text-transform: uppercase;
            font-family: 'Noticia Text', serif;
            font-size: 15px;
            color: #fff;
            line-height: 1em;
        }
        .adres{
            line-height: 1.5em;
            text-align: center;
            margin-top: 2em;
            color: #fff;
        }
        .adres1{
            line-height: 0.5em;
            text-align: center;
            margin-top: 2em;
            color: #fff;
        }
        a{
            padding: 5px 10px;
            width: 100%;
            margin: auto;
            color: #fff;
            margin-top: 2em;
        }
        .adres2{
            max-width: 100% !important;
            height: 30vh;          
            iframe{
            width: 100%;
            height: 20vh;
            margin-top: 2em;
            }
        }
         @media (max-width: 500px){
            h3{
            text-align: center;
            text-transform: uppercase;
            font-family: 'Noticia Text', serif;
            font-size: 15px;
            color: #fff;
            line-height: 1.2em;
        }
        .adres{
            line-height: 1.5em;
            text-align: center;
            margin-top: 1em;
            color: #fff;
            margin-bottom: 4em;
        }
       
        .adres1{
            margin-top: 4em;
            line-height: 0.5em;
            text-align: center;
            margin-top: 1em;
            color: #fff;
        }a{
            margin-bottom: 4em;
        }
         }   
    }
`
export default Footer
