import React, { useEffect, useContext } from 'react'
import { Context } from '../context'
import styled from 'styled-components'
import Bg from '../images/fixedbg.jpg'
import g1 from '../images/g1.png'
import g2 from '../images/g2.png'
import g3 from '../images/g3.png'
import g4 from '../images/g4.png'
import g5 from '../images/g5.jpg'
import g6 from '../images/g6.jpg'
import g7 from '../images/g7.jpg'
import g8 from '../images/g8.jpg'
import Aos from 'aos'
import "aos/dist/aos.css"

const Gallery = () => {
    const { gallery } = useContext(Context)
    useEffect(() => {
        Aos.init({
            duration: 3000
        })
    }, [])
    return (
        <Section>
            <div className="overlay"></div>
            <div className="container">
                <div data-aos="fade-up" className="header-text">
                    <span>RASMLAR</span>
                    <h1 className='title'>RESTORAN KO'RINISH</h1>
                </div>
                <div className="row row-cols-2 row-cols-lg-5 g-2 g-lg-3">
                    {gallery.map(el => {
                        return (
                            <div className="col-lg-3 col-md-6 col-sm-12">
                                <img src={el.image}  data-aos="fade-up" alt="" className="img img-fluid" />
                            </div>
                        )
                    })}
                    {/* <div data-aos="fade-up" className="col-lg-3 col-md-6 col-sm-12">
                        <img src={g2} alt="" className="img img-fluid" />
                    </div>
                    <div data-aos="fade-up" className="col-lg-3 col-md-6 col-sm-12">
                        <img src={g3} alt="" className="img img-fluid" />
                    </div>
                    <div data-aos="fade-up" className="col-lg-3 col-md-6 col-sm-12">
                        <img src={g4} alt="" className="img img-fluid" />
                    </div>
                    <div data-aos="fade-up" className="col-lg-3 col-md-6 col-sm-12">
                        <img src={g5} alt="" className="img img-fluid" />
                    </div>
                    <div data-aos="fade-up" className="col-lg-3 col-md-6 col-sm-12">
                        <img src={g6} alt="" className="img img-fluid" />
                    </div>
                    <div data-aos="fade-up" className="col-lg-3 col-md-6 col-sm-12">
                        <img src={g7} alt="" className="img img-fluid" />
                    </div>
                    <div data-aos="fade-up" className="col-lg-3 col-md-6 col-sm-12">
                        <img src={g8} alt="" className="img img-fluid" />
                    </div> */}
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
     background: url(${Bg}) no-repeat center center/cover fixed;
    padding: 5em 0;
    position: relative;
    z-index: 0;
    @media (max-width: 500px){
        padding: 2em 0;
    }
    .row{
        padding: 2.2em 0;
        @media (max-width: 500px){
            padding: 2em 0;
        }
    }
    .header-text{
        .title{
            color: #fac564;
            font-family: 'Open-sans';
        }
        span{
            color: white;
        }
    }
`
export default Gallery
