import React, {useEffect} from 'react'
import styled from 'styled-components'
import Aos from 'aos'
import "aos/dist/aos.css"

const Cards = () => {
    useEffect(() => {
        Aos.init({
            duration: 3000
        })
    }, [])
    return (
        <Section>
            <div className="card-header">
                <div className="container">
                    <span data-aos="fade-up" className="text-white">Nima uchun Biz</span>
                    <h1 data-aos="fade-up" className='title'>BIZNING RESTORANIMIZNI TANLAYSIZ</h1>
                    <div className="row">
                        <div data-aos="fade-up" className="col-lg-4 col-md-6 col-sm-12">
                            <div className="card">
                                <div className="text">
                                    <span>01</span>
                                    <h2 className="card-title">Lorem Ipsum</h2>
                                    <p>Ulamco laboris nisi ut aliquip ex ea commodo consequat. Et consectetur ducimus vero placeat</p>
                                </div>
                            </div>
                        </div>
                        <div data-aos="fade-up" className="col-lg-4 col-md-6 col-sm-12">
                            <div className="card">
                                <div className="text">

                                    <span>02</span>
                                    <h2 className="card-title">Repellat Nihil</h2>
                                    <p>Dolorem est fugiat occaecati voluptate velit esse. Dicta veritatis dolor quod et vel dire leno para dest</p>
                                </div>
                            </div>
                        </div>
                        <div data-aos="fade-up" className="col-lg-4 col-md-6 col-sm-12">
                            <div className="card">
                                <div className="text">

                                    <span>03</span>
                                    <h2 className="card-title">Ad ad velit qui</h2>
                                    <p>Molestiae officiis omnis illo asperiores. Aut doloribus vitae sunt debitis quo vel nam quis</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
    background: #0c0b09;
    /* height: 60vh; */
    padding: 5em 0;
    .row{
        padding: 2.2em 0;
        .card{
            padding: 2em 2em;
            background: #1a1814;
            height: 27vh;
            margin: 0  0 1.5em 0;
            .text{
                line-height: 4em;
                color: white;
                P{
                    line-height: 1em;
                }
                span{
                    font-size: 28px;
                    color: #fac564;
                    font-weight: 800;
                }
            }
        }
    }
    .title{
            color: #fac564;
            font-family: 'Open-sans';
        }
`
export default Cards
