import React, { useState, useEffect } from 'react'
import Bg from '../images/fixedbg.jpg'
import menuItems from '../menuItems'
import styled from 'styled-components'
import Aos from 'aos'
import "aos/dist/aos.css"

const Menu = () => {
    useEffect(() => {
        Aos.init({
            duration: 3000
        })
    }, [])
    const pizza = menuItems.filter(el => {
        return el.category == 'pizza'
    })
    const [filter, setFilter] = useState(pizza)

    const filterByCategoryHandler = (cat) => {
        const category = menuItems.filter(el => {
            return el.category == cat
        })
        setFilter(category)
    }
    return (
        <Main>
            <div className="tab-content">
                <div className="overlay"></div>
                <div className="container">
                    <div data-aos="fade-up" className="header-text">
                        <span>MENYU</span>
                        <h1 className='title'>MAZALI TAOMLARIMIZ RO'YXATI</h1>
                    </div>
                    <div data-aos="fade-up" className="mt-5">
                        <button className="button" onClick={() => filterByCategoryHandler('pizza')}>Kebab</button>
                        <button className="button" onClick={() => filterByCategoryHandler('drinks')}>Salat</button>
                        <button className="button" onClick={() => filterByCategoryHandler('burger')}>Sho'rva</button>
                        <button className="button" onClick={() => filterByCategoryHandler('burger')}>Durum</button>
                        <button className="button" onClick={() => filterByCategoryHandler('burger')}>Pizza</button>
                        <button className="button" onClick={() => filterByCategoryHandler('burger')}>Disert</button>
                    </div>
                    <div data-aos="fade-up" className="row">
                        {filter.map(el => {
                            return (
                                <div key={el.id} className="col-lg-6 col-md-6 col-sm-12">
                                    <div className="box d-flex justify-content-center align-items-center">
                                        <Div className="image">
                                            <img src={el.img} alt="image" title="image" className="imges" />
                                        </Div>
                                        <div className="caption">
                                            <a href="#">{el.name}</a>
                                            <p className="des"></p>
                                            <span className="text-white">{el.desc}</span>
                                            <div className="price">${el.price}</div>
                                        </div>
                                    </div>
                                </div>
                            )
                        })}
           
                    </div>
                </div>
            </div>
        </Main>
    )
}
const Div = styled.div`
width: 20%;
.imges{
    width: 90px;
    border-radius: 50%;
    float: left;
    border: 5px solid rgba(255, 255, 255, 0.2);
}
`
const Main = styled.div`
    background: #0c0b09;
    padding: 5em 0;
    @media (max-width: 500px){
        padding: 2em 0;
    }
    .header-text{
        .title{
            color: #fac564;
            font-family: 'Open-sans';
        }
        span{
            color: white;
        }
    }
    .row{
        padding: 4em 0;
    }

 .box .caption{
    /* position: relative; */
    /* margin-left: 10px; */
    /* padding: 20px 15px 20px 10px; */
    min-height: 100px;
    padding: 3em 0;
    @media (max-width: 500px){
        margin-left: 12%;
    }
    @media (max-width: 995px){
        margin-left: 12%;
    }
    @media (max-width: 1199px){
        margin-left: 12%;
    }
}
 .box .caption a{
    padding-right: 10px;
    position: relative;
    /* z-index: 3; */
    font-size: 1.5rem;
    font-weight: 700;
    color: #e54c2a;
    text-decoration: none;
    transition: 0.3s;
    font-family: "Josefin Sans", Arial, sans-serif;
    &:hover{
        color: #fac564;
    }
}.box .caption .des{
    margin-left: 85px;
    font-style: italic;
    font-size: 14px;
    font-family: "Poppins", sans-serif;
    color: rgba(255, 255, 255, 0.5);
    font-family: "Josefin Sans", Arial, sans-serif;
}.box .caption span{
    color:#757575;
    
}.box .caption .price{
    font-weight: 600;
    color: #cda45e;
    font-family: "Josefin Sans", Arial, sans-serif;
} 
.button{
    border-radius: 0;
    margin-bottom: 0;
    color: #fac564;
    font-size: 18px;
    font-weight: 400;
    display: inline-block;
    border: 1px solid #fac564;
    background: none;
    margin-left: 5px;
    margin-right: 5px;
    padding: 8px 10px;
    :hover{
        color: #fff;
        background: #fac564;
    }
    @media (max-width: 500px){
            display: block;
            width: 100%;
            margin: auto;
            margin-top: 10px;
        }
}
`
export default Menu
