import React, {useEffect} from 'react'
import styled from 'styled-components'
import Bg from '../images/aboutbg.jpg'
import about from '../images/about.jpg'
import Aos from 'aos'
import "aos/dist/aos.css"

const Haqida = () => {
    useEffect(() => {
        Aos.init({
            duration: 3000
        })
    }, [])
    return (
        <Section>
            <div className="overlay"></div>
            <div className="container">
                <div className="row">
                    <div className="col-lg-6 col-md-6 col-sm-12">
                        <div data-aos="fade-up" className="about-text">
                            <h3>Voluptatem dignissimos provident quasi corporis voluptates sit assumenda.</h3>
                            <p className="desc">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                            <p><i class="fal fa-check-double"></i>  Ullamco laboris nisi ut aliquip ex ea commodo consequat.</p>
                            <p><i class="fal fa-check-double"></i>  Duis aute irure dolor in reprehenderit in voluptate velit.</p>
                            <p><i class="fal fa-check-double"></i>Ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate trideta storacalaperda mastiro dolore eu fugiat nulla pariatur.</p>
                            <p>Ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum</p>
                        </div>
                    </div>
                    <div data-aos="fade-up" className="col-lg-6 col-md-6 col-sm-12">
                        <div className="about-img">
                            <img src={about} className="img img-fluid" alt="" />
                        </div>
                    </div>
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
    background: url(${Bg}) no-repeat center center/cover fixed;
    height: 60vh;
    position: relative;
    z-index: 0;
    @media (max-width: 500px){
        height: 100vh;
    }
    .row{
        height: 60vh;
        justify-content: center;
        align-items: center;
        .about-text{
            color: white;
            h3{
                font-family: 'Opan-sans';
            }
            i{
                color: #fac564;
            }
            .desc{
                font-style: italic;
            }
        }
    }
`
export default Haqida
