const Section = styled.div`
background: url(${Bg}) no-repeat center center/cover fixed;
    padding-bottom: 5em;
    position: relative;
    z-index: 0;
    .row{}
    .header-text{
        padding: 2.2em 0;
        .title{
            color: #fac564;
            font-family: 'Open-sans';
        }
        span{
            color: white;
        }
    }
    .carousel-inner{
        background: rgba(255, 255, 255, 0.08);
        padding: 40px;
        @media (max-width: 500px){
            padding: 20px;
        }
    }
    .about-text{
            color: white;
            line-height: 1em;
            h3{
                font-family: 'Opan-sans';
                color: #fac564;
            }
            i{
                color: #fac564;
            }
            .desc{
                font-style: italic;
            }
            .price{
                font-size: 18px;
                font-family: 'Opan-sans';
                font-weight: 900;
                color: #fac564;
                padding: 0.5em 0;
                border-bottom: 2px solid #fac564;
                width: 50px;
            }
        }
        .img{
            width: 80%;
            float: right;
            @media (max-width: 500px){
                width: 100%;
            }
        }
`
export default Marosim
