import React,{useEffect} from 'react'
import styled from 'styled-components'
import menu from '../images/g1.png'
import Aos from 'aos'
import "aos/dist/aos.css"
const Namecategory = () => {
    useEffect(() => {
        Aos.init({
            duration: 3000
        })
    }, [])
    return (
        <Section>
            <div className="container">
                <div data-aos="fade-up" class="section-title">
                    <span>TAOMLAR</span>
                    <h1>BIZNING MAXSUS TAOMLARNI KO'RING</h1>
                </div>
                <div data-aos="fade-up" className="row align-items-center">
                    <div className="col-lg-3 col-md-4 col-sm-12">
                        <ul class="nav nav-tabs flex-column">
                            <li class="nav-item">
                                <a class="nav-link" data-toggle="tab" href="#">МЯАСНОЙ АССОРТИ</a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" data-toggle="tab" href="#">КУРИНАЕ АССОРТИ</a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" data-toggle="tab" href="#">СМЕШАННЫЙ</a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" data-toggle="tab" href="#">КУШ БАФ ПИДЕ</a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" data-toggle="tab" href="#">ОТ ШЕФ ПОВАРА МЕТРОВЫИ КЕБАБ</a>
                            </li>
                        </ul>
                    </div>
                    <div className="col-lg-9 mt-4 mt-lg-0">
                        <div class="tab-content">
                            <div class="tab-pane show active" id="tab-1">
                                <div class="row align-items-center">
                                    <div class="col-lg-8 details order-2 order-lg-1">
                                        <h3>ETKARISIK IZGARA</h3>
                                        <p class="font-italic">1/83 000. 2/169 000. 4/294 000.</p>
                                        <p>БАРАНЬЯ ПИРЗОЛА, МОЛОТЫЙ ШАШЛЫК, ГОВЯЖИЙ ШАШЛЫК.</p>
                                    </div>
                                    <div class="col-lg-4 text-center order-1 order-lg-2">
                                        <img src={menu} alt="" class="img-fluid" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
            background: #0c0b09;
            padding: 5em 0;
            @media (max-width: 500px){
                padding: 2em 0;
            }
            .section-title{
                h1{
                color: #fac564;
            font-family: 'Open-sans';
            }
        }
            span{
                color: white;
        }
            .row{
                padding: 2.2em 0;
                .details h3 {
                    font-size: 26px;
                    font-weight: 600;
                    margin-bottom: 20px;
                    color: #fff;
                    font-family: 'Open-sans';
                }
                .font-italic {
                    font-style: italic!important;
                    color: gray;
                }
                .details p {
                    color: #aaaaaa;
                }
                .img-fluid {
                    max-width: 100%;
                    height: auto;
                }
        }
        .nav-link {
            padding: 12px 15px;
            transition: 0.3s;
            color: #fff;
            border-radius: 0;
            border-right: 2px solid #cda45e;
            font-weight: 600;
            font-size: 15px;
            &:hover {
                color: #1a1814;
                background: #cda45e;
                border-color: #cda45e;
            }
            a{
                text-decoration: none;
            }

        }
        
            `
export default Namecategory



