import React, { useState, useEffect, useContext } from 'react'
import styled from 'styled-components'
import { Context } from '../context'
import Bg from '../images/fixedbg.jpg'
// import Ali from '../images/Aliy.jpg'
// import chef from '../images/chefs2.jpg'
// import chef2 from '../images/chefs3.jpg'
const Oshpazlar = () => {
    const { data } = useContext(Context)


    return (
        <Section>
            <div className="overlay"></div>
            <div className="container">
                <div className="header-text">
                    <span>OSHPAZLAR</span>
                    <h1 className='title'>PROFESSIONAL OSHPAZLAR</h1>
                </div>
                <div className="row">
                    {data.map(el => {
                        return (
                            <div key={el.id} className="col-lg-4 col-md-4 col-sm-12">
                                <img src={el.image} alt="" className="img img-fluid" />
                                <div class="over">
                                    <h3>{el.full_name}</h3>
                                    <span>{el.position}</span>
                                    <div className="icon-group">
                                        <a href=""><i class="fab fa-facebook-f"></i></a>
                                        <a href=""><i class="fab fa-instagram ms-4"></i></a>
                                        <a href=""><i class="fab fa-telegram-plane ms-4"></i></a>
                                    </div>
                                </div>
                            </div>
                        )
                    })}
                    {/* <div className="col-lg-4 col-md-4 col-sm-12">
                        <img src={chef} alt="" className="img img-fluid" />
                        <div class="over">My Name is John</div>
                    </div>
                    <div className="col-lg-4 col-md-4 col-sm-12">
                        <img src={chef2} alt="" className="img img-fluid" />
                        <div class="over">My Name is John</div>
                    </div> */}
                </div>
            </div>

        </Section>
    )
}
const Section = styled.div`
    background: url(${Bg}) no-repeat center center/cover fixed;
    padding: 5em 0;
    position: relative;
    z-index: 0;
    @media (max-width: 500px){
        padding: 2em 0;
    }
    .row{
        padding: 2.3em 0;
       
        .col-lg-4{
            position: relative;
            img{
                display: block;
                width: 100%;
                height: auto;
                @media (max-width: 500px){
                    margin: 1em 0;
                }
            }
            .over{
                position: absolute; 
                bottom: 0; 
                background: rgb(0, 0, 0);
                background: rgba(0, 0, 0, 0.5); /* Black see-through */
                color: #f1f1f1; 
                width: 100%;
                transition: .5s ease;
                opacity:0;
                color: white;
                font-size: 20px;
                padding: 40px;
                text-align: center;
                a{
                    text-decoration: none;
                    color: white;
                }
                &:hover{
                    opacity: 1;
                }
            }
        }
    }
    .header-text{
        .title{
            color: #fac564;
            font-family: 'Open-sans';
        }
        span{
            color: white;
        }
    }
`
export default Oshpazlar
