import React, { useContext } from 'react'
import styled from 'styled-components'
import Bg from '../images/fixedbg.jpg'
import { Context } from '../context'


const Contact = () => {
    const { sit } = useContext(Context)
    return (
        <Main>
            <div className="container">
                <div className="overlay"></div>
                <div className="header-text">
                    <span>Nima uchun Biz</span>
                    <h1 className='title'>BIZNING RESTORANIMIZNI TANLAYSIZ</h1>
                </div>
                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2996.389359407276!2d69.30142231566913!3d41.32214600799959!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x38aef4c4c25bf0c3%3A0xf3b25a701b983beb!2z0YPQuy4g0JDQutC60YPRgNCz0LDQvQ!5e0!3m2!1sru!2s!4v1629373279345!5m2!1sru!2s" allowfullscreen="" loading="lazy"></iframe>

                {sit.map(el => {
                    return (
                        <div className="row justify-content-center align-items-center">
                            <div className="col-lg-6 col-md-6 col-sm-12">
                                <div className="location d-flex justify-content-center align-items-center">
                                    <i class="fal fa-location fa-2x text-light p-2 "></i>
                                    <p className="ms-2 text-white">Location:<br/>{el.location}</p>
                                </div>
                                <div className="location d-flex align-items-center">
                                    <i class="far fa-clock fa-2x text-light p-2 "></i>
                                    <p className="ms-2 text-white">Открыто:<br/>
                                        {el.open_time} AM - {el.close_time} PM</p>
                                </div>
                                <div className="location d-flex align-items-center">
                                    <i class="fal fa-envelope fa-2x text-light p-2 "></i>
                                    <p className="ms-2 text-white">Email:<br/>{el.email}</p>
                                </div>
                                <div className="location d-flex align-items-center">
                                    <i class="far fa-phone fa-2x text-light p-2 "></i>
                                    <p className="ms-2 text-white">Phone<br/>{el.phone}</p>
                                </div>
                            </div>
                            <div className="col-lg-6 col-md-6 col-sm-12">
                                <form action="#" className="contact-form m-auto py-5">
                                    <div className="row">
                                        <div className="col-md-6">
                                            <div className="form-group">
                                                <input type="text" className="form-control" placeholder="Имя:" />
                                            </div>
                                        </div>
                                        <div className="col-md-6">
                                            <div className="form-group">
                                                <input type="text" className="form-control" placeholder="Фамилия:" />
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-group my-3">
                                        <textarea name="" id="" cols="30" rows="5" className="form-control" placeholder="Комментария:" />
                                    </div>
                                    <a href="#" className="btn text-center m-auto">Отправить</a>
                                </form>
                            </div>
                        </div>
                    )
                })}

            </div>
        </Main>
    )
}
const Main = styled.div`
 background: url(${Bg}) no-repeat center center/cover fixed;
    padding: 5em 0;
    position: relative;
    z-index: 0;
padding: 2em 0;
iframe{
    width: 100%;
    height: 40vh;
    padding: 3em 0;
}
     .header-text{
        .title{
            color: #fac564;
            font-family: 'Open-sans';
        }
        span{
            color: white;
        }
    }
    form{
        max-width: 100%;
        @media (max-width: 500px){
            max-width: 90%;
        }
        .btn{
            outline: none;
            border: 1px solid white;
            color: white;
        }
    }
    .form-title{
        font-weight: bold;
        font-size: 18px;
        font-family: 'Andada Pro', serif;
        text-align: center;
    }
    .form-group{
        input{
            background: none;
        }
        textarea{
            border: 1px solid white;
            background: none;
            resize: none;
        }
        
    }
    .location{
         padding: 2em 0 0 0;
        p{
            line-height: 1.3em;
        }
        i{
            border: 1px solid white;
            border-radius: 25px;
            background: #fac564;
            text-align: start;
        }
    }
`
export default Contact
