import React from 'react'
import {Switch ,Route} from 'react-router-dom'
import Menyu from './pages/Menyu'
import Rasimlar from './pages/Rasimlar'
import Aloqa from './pages/Aloqa'
import Video from './pages/Video';
import Asosiy from './pages/Asosiy'

function App() {
  return (
      <Switch>
        <Route exact path="/">
           <Asosiy/>
        </Route>
        <Route exact path="/menyu">
           <Menyu/>
        </Route>
        <Route exact path="/rasimlar">
           <Rasimlar/>
        </Route>
        <Route exact path="/video">
           <Video/>
        </Route>
        <Route exact path="/aloqa">
           <Aloqa/>
        </Route>
      </Switch>
  );
}

export default App;
