import React, {createContext, useState ,useEffect} from 'react'
import menuItems from './menuItems'
const url = "http://127.0.0.1:8000/api/cooker/"
const gal = "http://127.0.0.1:8000/api/gallery/"
const site = "http://127.0.0.1:8000/api/site/"
const event = "http://127.0.0.1:8000/api/event/"
const Context = createContext()
const ContextProvider = ({children}) => {


    const [cart, setCart] = useState([]) 
    const [data, setData] = useState([])
    const [gallery, setGallery] = useState([])
    const [sit, setSit] = useState([])
    const [eva, setEva] = useState([])

    const getProducts = async () => {
        const response = await fetch(url)
        const data = await response.json()
        setData(data)
    }

    useEffect(() => {
        getProducts()
    }, [])

// GALLERY


const getGallery = async () => {
    const response = await fetch(gal)
    const gallery = await response.json()
    setGallery(gallery)
}

useEffect(() => {
    getGallery()
}, [])


//Gallery

//SITE

const getSite = async () => {
    const response = await fetch(site)
    const sit = await response.json()
    setSit(sit)
}

useEffect(() => {
    getSite()
}, [])

//SITE
//EVENT

const getEvent = async () => {
    const response = await fetch(event)
    const eva = await response.json()
    setEva(eva)
}

useEffect(() => {
    getEvent()
}, [])

//EVENT

    const pizza = menuItems.filter(el => {
        return el.category == 'pizza'
    })
    const [filter, setFilter] = useState(pizza)

    const filterByCategoryHandler = (cat) => {
        const category = menuItems.filter(el => {
            return el.category == cat
        })
        setFilter(category)
    }

    function addtoCart(el){
        console.log(el)
        setCart([...cart, el])
        console.log(cart)
    }
    const name = 'Sanjar'
    return (
        <Context.Provider value={{addtoCart, cart, filterByCategoryHandler, data, gallery, sit, eva}}>
            {children}
        </Context.Provider>
    )
}

export  {ContextProvider, Context}
